#move-list {
  position: fixed;
  left: 0;
  width: 150px;
  height: 100%; }
  #move-list li {
    text-align: center; }
    #move-list li button {
      min-width: 100%;
      min-height: auto;
      padding: 0; }
    #move-list li.title,
    #move-list li.active > button {
      font-weight: bold; }
    #move-list li.active > button {
      background-color: transparent !important;
      color: inherit !important; }



